import React, { Component } from 'react';
import '../styles/component.css';
import logo from '../images/duradek_logo.png';

const Nav = require('react-bootstrap/lib/Nav');
const Navbar = require('react-bootstrap/lib/Navbar');
const NavItem = require('react-bootstrap/lib/NavItem');
const NavDropdown = require('react-bootstrap/lib/NavDropdown');
const MenuItem = require('react-bootstrap/lib/MenuItem');

class Navigation extends Component {
  render() {
    return (
    	<div>
			<Navbar inverse collapseOnSelect fixedTop>
			  <Navbar.Header>
			    <Navbar.Brand>
			      <a href="/"><img src={logo} alt="duradek construction vinyl" height="40px" className="logo"/></a>
			    </Navbar.Brand>
			    <Navbar.Toggle />
			  </Navbar.Header>
			  <Navbar.Collapse>
			    <Nav pullRight>
			      <NavItem eventKey={1} href="/">
			        Home
			      </NavItem>
			      <NavItem eventKey={2} href="/about">
			        About Us
			      </NavItem>
			      <NavDropdown eventKey={3} title="Services" id="basic-nav-dropdown">
			        <MenuItem eventKey={3.1}>Waterproofing</MenuItem>
			        <MenuItem eventKey={3.2}>Railings</MenuItem>
			        <MenuItem eventKey={3.3}>Canopies</MenuItem>
			        <MenuItem eventKey={3.3}>Renovations</MenuItem>
			      </NavDropdown>
			      <NavItem eventKey={2} href="/contact">
			        Contact
			      </NavItem>
			    </Nav>
			  </Navbar.Collapse>
			</Navbar>
    	</div>
    );
  }
}

export default Navigation;



