import React, { Component } from 'react';
import '../styles/component.css';
import deck from '../images/deckBanner.JPG';
import site from '../images/siteBanner.JPG';

const Carousel = require('react-bootstrap/lib/Carousel');
const Button = require('react-bootstrap/lib/Button');

class CarouselComp extends Component {
  render() {
    return (
      <div className="carousel">
        <Carousel controls={false} pauseOnHover={false}>
          <Carousel.Item>
            <img className="slide0" alt="900x500" src={deck} />
          </Carousel.Item>
          <Carousel.Item>
            <img alt="900x500" src={site} />
          </Carousel.Item>
        </Carousel>
        <div className="blanket"></div>
        <div className="hoverDiv">
          <h1>Good Company</h1>
          <h3>Something that makes people like you</h3>
          <Button bsSize="large" className="button"><b>GET A QUOTE</b></Button>
        </div>
      </div>
    );
  }
}

export default CarouselComp;



